import { IAction } from '../action-creators/actions';
import { ActionTypes } from '../actionTypes';
import { IApplicationState } from '../applicationState';

export default (state: IApplicationState, action: IAction) => {
  switch (action.type) {
    case ActionTypes.HELLO:
      return state;
    case ActionTypes.INCREMENT:
      return state;
    default:
      return state;
  }
}


