import { delay } from 'redux-saga';
import { put, takeLatest } from 'redux-saga/effects';
import { IAction } from '../action-creators/actions';
import { increment } from '../action-creators/async';
import { ActionTypes } from '../actionTypes';

function *incrementAsync(action: IAction) {
  console.log('Delaying');
  yield delay(1000);
  console.log('INCREMENTING');
  yield put(increment());
}

export function *incrementAsyncWatcher() {
  yield takeLatest(ActionTypes.ASYNC, incrementAsync);
}



