import * as React from 'react';
import { connect } from 'react-redux';
import { Dispatch } from 'redux';
import { IApplicationState } from './store/applicationState';

import { Button } from 'antd';

import { apiAsync } from './store/action-creators/async';
import { hello } from './store/action-creators/hello';

import './App.css';

import logo from './logo.svg';

interface IPropsFromState {
  strHello?: string,
}

interface IPropsFromDispatch {
  apiHello?: any
  apiAsync?: any
}

type IComponentProps = IPropsFromState & IPropsFromDispatch;

const mapStateToProps = (state: IApplicationState) => {
  return {
  };
};

const mapDispatchToProps = (dispatch: Dispatch) => ({
  apiAsync: () => dispatch(apiAsync()),
  apiHello: () => dispatch(hello()),
});

class App extends React.Component<IComponentProps> {

  public render() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1 className="App-title">Welcome to React</h1>
        </header>
        <p className="App-intro">
          To get started, edit <code>src/App.tsx</code> and save to reload.
        </p>
        <Button onClick={this.handleOnClick}>Click here to start edited</Button>
      </div>
    );
  }

  private handleOnClick = () => {
    this.props.apiAsync();
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(App);



